// axios  
import isString from 'lodash-es/isString';
import merge from 'lodash-es/merge';
import type { AxiosTransform, CreateAxiosOptions } from './transform';
import { VAxios } from './axios';
import { joinTimestamp, formatRequestDate, setObjToUrlParams } from './util';
import { TOKEN_NAME } from '@/config/global';
import { handleError } from './handle-error';

// 
const transform: AxiosTransform = {
  // 
  transformRequestHook: (res, options) => {
    const { isTransformResponse, isReturnNativeResponse } = options;

    // 204
    const method = res.config.method?.toLowerCase();
    if (res.status === 204 || method === 'put' || method === 'patch') {
      return res;
    }

    //  
    if (isReturnNativeResponse) {
      return res;
    }
    // 
    // codedatamessage
    if (!isTransformResponse) {
      return res.data;
    }

    // 
    const { data } = res;
    if (!data) {
      throw new Error('');
    }

    //   code  types.ts
    const { code, msg } = data;

    // 
    const hasSuccess = data && code === 0;
    if (hasSuccess) {
      return data.data;
    }

    throw new Error(`, : ${code}, : ${msg}`);
  },

  // 
  beforeRequestHook: (config, options) => {
    const {
      apiUrl,
      isJoinPrefix,
      urlPrefix,
      joinParamsToUrl,
      formatDate,
      joinTime = true
    } = options;

    // 
    if (isJoinPrefix && urlPrefix && isString(urlPrefix)) {
      config.url = `${urlPrefix}${config.url}`;
    }

    // baseUrl
    if (apiUrl && isString(apiUrl)) {
      config.url = `${apiUrl}${config.url}`;
    }
    const params = config.params || {};
    const data = config.data || false;

    if (formatDate && data && !isString(data)) {
      formatRequestDate(data);
    }
    if (config.method?.toUpperCase() === 'GET') {
      if (!isString(params)) {
        //  get 
        config.params = Object.assign(
          params || {},
          joinTimestamp(joinTime, false)
        );
      } else {
        // restful
        config.url = `${config.url + params}${joinTimestamp(joinTime, true)}`;
        config.params = undefined;
      }
    } else if (!isString(params)) {
      if (formatDate) {
        formatRequestDate(params);
      }
      if (
        Reflect.has(config, 'data') &&
        config.data &&
        (Object.keys(config.data).length > 0 || data instanceof FormData)
      ) {
        config.data = data;
        config.params = params;
      } else {
        // GETdataparamsdata
        config.data = params;
        config.params = undefined;
      }
      if (joinParamsToUrl) {
        config.url = setObjToUrlParams(config.url as string, {
          ...config.params,
          ...config.data
        });
      }
    } else {
      // restful
      config.url += params;
      config.params = undefined;
    }
    return config;
  },

  // 
  requestInterceptors: (config, options) => {
    // config
    const token = localStorage.getItem(TOKEN_NAME);
    if (token && (config as Recordable)?.requestOptions?.withToken !== false) {
      // jwt token
      (config as Recordable).headers.Authorization =
        options.authenticationScheme
          ? `${options.authenticationScheme} ${token}`
          : token;
    }
    return config;
  },

  // 
  responseInterceptors: res => {
    return res;
  },

  // 
  responseInterceptorsCatch: (error: any) => {
    const { config } = error;
    handleError(error);
    if (!config || !config.requestOptions.retry) {
      return Promise.reject(error);
    }

    config.retryCount = config.retryCount || 0;

    if (config.retryCount >= config.requestOptions.retry.count) {
      return Promise.reject(error);
    }

    config.retryCount += 1;

    const backoff = new Promise(resolve => {
      setTimeout(() => {
        resolve(config);
      }, config.requestOptions.retry.delay || 1);
    });

    return backoff.then(config => request.request(config));
  }
};

function createAxios(opt?: Partial<CreateAxiosOptions>) {
  return new VAxios(
    merge(
      <CreateAxiosOptions>{
        // https://developer.mozilla.org/en-US/docs/Web/HTTP/Authentication#authentication_schemes
        // : authenticationScheme: 'Bearer'
        authenticationScheme: '',
        // 
        timeout: 10 * 1000,
        // Cookie
        withCredentials: true,
        // 
        headers: { 'Content-Type': 'application/json;charset=UTF-8' },
        // 
        transform,
        // 
        requestOptions: {
          // 
          // 
          isJoinPrefix: true,
          // 
          // : https://www.baidu.com/api
          // urlPrefix: '/api'
          //  
          isReturnNativeResponse: false,
          // 
          isTransformResponse: true,
          // posturl
          joinParamsToUrl: false,
          // 
          formatDate: true,
          // 
          joinTime: true,
          // 
          ignoreRepeatRequest: true,
          // token
          withToken: true,
          // 
          retry: {
            count: 3,
            delay: 1000
          },
          // 
          showErrorMessage: true
        }
      },
      opt || {}
    )
  );
}

const request = createAxios();
export default request;
