/*
  

  
    1absoluterelative
    2(onmousedown) left  top 
    3(onmousemove) left  top 
    4(onmouseup)
  
   Dom  v-draggable 
  <div class="dialog-model" v-draggable></div>
*/
import type { App } from 'vue';

interface ElType extends HTMLElement {
  parentNode: any;
}
function draggable(el: ElType) {
  el.style.cursor = 'move';
  el.style.position = 'absolute';
  el.onmousedown = function (e) {
    const disX = e.pageX - el.offsetLeft;
    const disY = e.pageY - el.offsetTop;
    document.onmousemove = function (e) {
      let x = e.pageX - disX;
      let y = e.pageY - disY;
      const maxX =
        parseInt(window.getComputedStyle(el.parentNode).width) -
        parseInt(window.getComputedStyle(el).width);
      const maxY =
        parseInt(window.getComputedStyle(el.parentNode).height) -
        parseInt(window.getComputedStyle(el).height);
      if (x < 0) {
        x = 0;
      } else if (x > maxX) {
        x = maxX;
      }

      if (y < 0) {
        y = 0;
      } else if (y > maxY) {
        y = maxY;
      }
      el.style.left = x + 'px';
      el.style.top = y + 'px';
    };
    document.onmouseup = function () {
      document.onmousemove = document.onmouseup = null;
    };
  };
}

export function draggableDirective(app: App) {
  app.directive('draggable', {
    //  DOM 
    mounted(el, binding) {
      draggable(el);
    }
  });
}
