<template>
  <svg
    :class="svgClass"
    aria-hidden="true"
  >
    <use
      class="svg-use"
      :href="symbolId"
    />
  </svg>
</template>

<script>
import { defineComponent, computed } from 'vue';

export default defineComponent({
  props: {
    prefix: {
      type: String,
      default: 'icon'
    },
    name: {
      type: String,
      required: true
    },
    className: {
      type: String,
      default: ''
    }
  },
  setup(props) {
    const symbolId = computed(() => `#${props.name}`);
    const svgClass = computed(() => {
      if (props.className) {
        return `svg-icon ${props.className}`;
      }
      return 'svg-icon';
    });
    return { symbolId, svgClass };
  }
});
</script>
<style scope>
.svg-icon {
  overflow: hidden;
  vertical-align: -0.1em; /* iconspan */
  fill: currentColor; /* currentColorcolorcolor */
}
</style>
