/**
 *   
 *  
 *  @param {?Number|300} delay - 
 *  @param {Function} fn - 
 *  @param {?String|"click"} event -  "click"
 *  @param {Array} binding.value - [fn,event,delay]
 *  <el-button v-throttle="[reset,`click`,500]"></el-button>
 */
import type { App, DirectiveBinding } from 'vue';

function throttle(el: HTMLElement, binding: DirectiveBinding) {
  const [fn, event = 'click', delay = 500] = binding.value;
  let timer: NodeJS.Timeout | null;
  el.addEventListener(event, () => {
    if (timer) return;
    timer = setTimeout(() => {
      fn();
      timer = null;
    }, delay);
  });
}

export function throttleDirective(app: App) {
  app.directive('throttle', {
    //  DOM 
    mounted(el, binding) {
      throttle(el, binding);
    }
  });
}
