type MsgDataType = typeof msgData;

export const useNotificationStore = defineStore({
  id: 'notification',
  state: () => ({
    msgData
  }),
  getters: {
    unreadMsg: state =>
      state.msgData.filter((item: NotificationItem) => item.status),
    readMsg: state =>
      state.msgData.filter((item: NotificationItem) => !item.status)
  },
  actions: {
    setMsgData(data: MsgDataType) {
      this.msgData = data;
    }
  },
  persist: true
});
